import { KEY_COMPONENT_NAME } from '../global/variable'
import { validatenull } from './validate'

const dateList = [
    'dates',
    'date',
    'datetime',
    'datetimerange',
    'daterange',
    'week',
    'month',
    'monthrange',
    'dategrpup',
    'year'
];
let typeMap = {
    'select': "select",
    "radio": "radio",
    "checkbox": "checkbox",
    "cascader": "cascader",
    "rate": "rate",
    "upload": "upload",
    "slider": "slider",
    "dynamic": "dynamic",
    "color": "color",
    "time": "time",
    'timerange': 'time',
    'icon-select': 'icon-select',
    switch: 'switch',
    number: 'input-number',
    password: 'input',
}

export const getComponent = function (type, component) {
    let result = 'input';
    if (!validatenull(component)) {
        result = component;
    } else if (typeMap[type]) {
        result = typeMap[type];
    } else if (dateList.includes(type)) {
        result = 'date';
    }
    return KEY_COMPONENT_NAME + result;
}

export const getPlaceholder = function (column, type) {
    // return column.placeholder;
    const placeholder = column.placeholder;
    const label = column.label;
    if (type === 'search') {
        const searchPlaceholder = column.searchPlaceholder;
        if (!validatenull(searchPlaceholder)) {
            return searchPlaceholder;
        } else {
            return label;
        }
    } else if (validatenull(placeholder)) {
        if (['select', 'checkbox', 'radio', 'tree'].includes(column.type)) {
            return `请选择 ${label}`;
        } else {
            return `请输入 ${label}`;
        }
    }

    return placeholder;
}

/**
 * 初始化数据格式
 */
export const initVal = ({ listType, type, multiple, dataType, value, curentForm }) => {
    if (
        (['select', 'tree'].includes(type) && multiple) ||
        ['checkbox', 'cascader', 'dynamic', 'upload'].includes(type)
    ) {
        // 头像框特殊处理
        if (listType === 'picture-img' && type === 'upload') {
            return [value];
        }
        if (Array.isArray(value)) return value;
        else if (!validatenull(value)) {
            const list = (value || '').split(',') || [];
            if (dataType === 'number') {
                return list.map(ele => Number(ele));
            } else {
                return list;
            }
        } else {
            return [];
        }
    }
    // 日期处理，不是很恰当，后面可能需要去除
    if (dateList.includes(type) || ['time', 'timerange'].includes(type)) {
        if (value instanceof Array && value.length > 0) {
            value = value.map(date => {
                if (validatenull(date)) {
                    return new Date();
                }
                if (typeof date === 'string') {
                    return new Date(date);
                } else {
                    return date;
                }
            });
        } else if (typeof value === "string") {
            value = new Date(value);
        }
    }
    // 如果是范围滑块，则需要初始化为[0,0]
    if (type === 'slider') {
        if (curentForm.range) {
            if (!value.length) value = [0, 0];
            value = value.map(item => {
                return (validatenull(item) || isNaN(item)) ? 0 : item;
            })
        }
    }
    return value;
};
/**
 * 计算级联属性
 */
export const calcCascader = (list = []) => {
    list.forEach((ele, index) => {
        if (ele.cascaderItem) {
            let cascader = [...ele.cascaderItem];
            let parentProp = ele.prop;
            list[index].cascader = [...cascader];
            cascader.forEach((citem, cindex) => {
                const columnIndex = index + cindex + 1;
                list[columnIndex].parentProp = parentProp;
                list[columnIndex].cascaderChange = ele.cascaderChange;
                list[columnIndex].cascader = [...cascader].splice(cindex + 1);
                parentProp = list[columnIndex].prop;
            });
        }
    });
    return list;
};
/**
 * 计算空白列row
 */
let count = 0;
export const calcCount = (ele, spanDefault = 12, init = false) => {
    if (init) count = 0;
    const spanAll = 24;
    count = count + (ele.span || spanDefault) + (ele.offset || 0);
    if (count === spanAll) {
        count = 0;
    } else if (count > spanAll) {
        count = 0 + (ele.span || spanDefault) + (ele.offset || 0);
    } else if (ele.row && count !== spanAll) {
        ele.count = spanAll - count;
        count = 0;
    }
    return ele;
};
/**
 * 表格初始化值
 */
export const formInitVal = (list = []) => {
    let tableForm = {};
    let searchForm = {};
    list.forEach(ele => {
        if (
            ele.type === 'checkbox' ||
            ele.type === 'cascader' ||
            ele.type === 'dynamic' ||
            ele.type === 'dates' ||
            (ele.type === 'upload' && ele.listType !== 'picture-img') ||
            ele.multiple ||
            ele.range ||
            ele.dataType === 'array'
        ) {
            tableForm[ele.prop] = [];
            if (ele.search) searchForm[ele.prop] = [];
        } else if (
            ['number', 'rate', 'slider'].includes(ele.type) ||
            ele.dataType === 'number'
        ) {
            tableForm[ele.prop] = undefined;
            if (ele.search) {
                searchForm[ele.prop] = undefined;
            }
        } else {
            tableForm[ele.prop] = '';
            if (ele.search) {
                searchForm[ele.prop] = '';
            }
        }
        // 表单默认值设置
        if (!validatenull(ele.valueDefault)) tableForm[ele.prop] = ele.valueDefault;
        // 搜索表单默认值设置
        if (!validatenull(ele.searchDefault)) {
            searchForm[ele.prop] = ele.searchDefault;
        }
    });
    return {
        tableForm,
        searchForm
    };
};


